@media screen and (min-width: 1200px) {
   .main-title {
      &__title {
      font-size: 144px;
      }
   }
   .footer-bottom {
      &__phone {
         font-size: 24px;
      }
   }
}
@media screen and (max-width: 1024px) {
   .header-navigation {
      margin-top: 10px;
   }
   .nav {
		&__link {
         font-size: 14px;
		}
   }
   .contacts {
      &__phone {
         font-size: 14px;
      }
   }
   .main-title {
      &__title {
      letter-spacing: 0.225em;
      }
   }
   .section {
      margin: 150px 0 0;
   }
   .content-about {
      display: flex;
      justify-content: space-between;

         &__body-container {
            flex-direction: column;
         }
         &__body-content {
            max-width: 100%;
            margin-left: 0;
         }
         &__paragraph {
            max-width: 100%;
            margin-bottom: 30px;
         }
         &__body-img {
            width: 100%;
            max-width: 640px;
            & img {
               width: 100%;
            }
         }
   }
   .housing .content-about {
      &__body-container {
         flex-direction: column-reverse;
      }
   }
   .section-title {
      margin-bottom: 30px;
   }
}
@media screen and (max-width: 991px) {
   .burger {
      display: flex;
   }
   .header {
      position: relative;
      z-index: 10;
   }
   .nav-wrapper {
      position: relative;
      &:before {
         display: block;
         content: "";
         width: 100%;
         height: 68px;
      }
   }
   .nav {
      display: block;
      width: 100%;
      position: fixed;
      top: 0;
      left: -100%;
      height: 100%;
      margin-top: 22px;
      overflow: auto;
      z-index: -1;

		&__list {
         display: block;
		}
		&__item {
         margin-bottom: 40px;
         margin-left: 20px;
         &:first-child {
            margin-left: 20px;

         }
		}
      &__link {
         font-size: 18px;
		}
   }
   .menu--active {
      left: 0;
   }
   .contacts {
      &__btn {
            padding: 10px 12px;
         }
   }
   .footer-content {
      padding: 30px 0;
      flex-direction: column;
      &__left {
         margin-bottom: 50px;
      }
   }
   .cpecification {
      &__item-img {
         display: none;
      }
   }
   .tabs {
      margin-top: 30px;
   }
}
@media screen and (max-width: 768px) {
   .main-title {
      &__title {
      font-size: calc(38px + (106 + 106 * 0.7) * ((100vw - 320px) / 1200));
      }
   }
}
@media screen and (max-width: 640px) {
   .section {
      margin: 120px 0 0;
   }
   .content-about {
      flex-direction: column;
   }
   .tabs.content-about {
      flex-direction: column-reverse;
   }
   .about-video {
      &__img {
         width: 60px;
         height: 60px;
      }
   }
}
@media screen and (max-width: 520px) {
   .section {
      margin: 50px 0 0;
   }
   .contacts {
		&__phone {
         margin-left: 20px;
         font-size: 18px;
		}
   }
   .footer-content {
      &__right {
         flex-direction: column;
      }
      &__right-column {
         margin-bottom: 30px;
         &:last-child {
            margin-bottom: 0;
         }
      }
   }
   .footer-bottom__phone {
      display: none;
   }
   .cpecification {
      &__item {
         flex-direction: column;
      }
      &__item-title {
         margin-bottom: 6px;
      }
   }
}
@media screen and (max-width: 320px) {
   .main-title {
         &__title {
         font-size: 44px;
      }
   }
}
